import { showError, showSuccess } from '@nextcloud/dialogs';
import { translate as t } from '@nextcloud/l10n';
import { getUploader } from '@nextcloud/upload';
import { defineComponent } from 'vue';
import TrayArrowDownIcon from 'vue-material-design-icons/TrayArrowDown.vue';
import logger from '../logger.js';
export default defineComponent({
  name: 'DragAndDropNotice',
  components: {
    TrayArrowDownIcon
  },
  props: {
    currentFolder: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      dragover: false
    };
  },
  mounted() {
    // Add events on parent to cover both the table and DragAndDrop notice
    const mainContent = window.document.querySelector('main.app-content');
    mainContent.addEventListener('dragover', this.onDragOver);
    mainContent.addEventListener('dragleave', this.onDragLeave);
  },
  beforeDestroy() {
    const mainContent = window.document.querySelector('main.app-content');
    mainContent.removeEventListener('dragover', this.onDragOver);
    mainContent.removeEventListener('dragleave', this.onDragLeave);
  },
  methods: {
    onDragOver(event) {
      const isForeignFile = event.dataTransfer?.types.includes('Files');
      if (isForeignFile) {
        // Only handle uploading
        this.dragover = true;
      }
    },
    onDragLeave( /* event: DragEvent */
    ) {
      if (this.dragover) {
        this.dragover = false;
      }
    },
    onDrop(event) {
      if (this.$el.querySelector('tbody')?.contains(event.target)) {
        return;
      }
      event.preventDefault();
      event.stopPropagation();
      if (event.dataTransfer && event.dataTransfer.files?.length > 0) {
        const uploader = getUploader();
        uploader.destination = this.currentFolder;
        // Start upload
        logger.debug(`Uploading files to ${this.currentFolder.path}`);
        const promises = [...event.dataTransfer.files].map(async file => {
          try {
            return await uploader.upload(file.name, file);
          } catch (e) {
            showError(t('files', 'Uploading "{filename}" failed', {
              filename: file.name
            }));
            throw e;
          }
        });
        // Process finished uploads
        Promise.all(promises).then(uploads => {
          logger.debug('Upload terminated', {
            uploads
          });
          showSuccess(t('files', 'Upload successful'));
          // Scroll to last upload if terminated
          const lastUpload = uploads[uploads.length - 1];
          if (lastUpload?.response?.headers?.['oc-fileid']) {
            this.$router.push({
              ...this.$route,
              params: {
                // Remove instanceid from header response
                fileid: parseInt(lastUpload.response?.headers?.['oc-fileid'])
              }
            });
          }
        });
      }
    },
    t
  }
});