import { formatFileSize } from '@nextcloud/files';
import { translate } from '@nextcloud/l10n';
import Vue from 'vue';
import { useFilesStore } from '../store/files.ts';
import { usePathsStore } from '../store/paths.ts';
export default Vue.extend({
  name: 'FilesListTableFooter',
  components: {},
  props: {
    isMtimeAvailable: {
      type: Boolean,
      default: false
    },
    isSizeAvailable: {
      type: Boolean,
      default: false
    },
    nodes: {
      type: Array,
      required: true
    },
    summary: {
      type: String,
      default: ''
    },
    filesListWidth: {
      type: Number,
      default: 0
    }
  },
  setup() {
    const pathsStore = usePathsStore();
    const filesStore = useFilesStore();
    return {
      filesStore,
      pathsStore
    };
  },
  computed: {
    currentView() {
      return this.$navigation.active;
    },
    dir() {
      // Remove any trailing slash but leave root slash
      return (this.$route?.query?.dir || '/').replace(/^(.+)\/$/, '$1');
    },
    currentFolder() {
      if (!this.currentView?.id) {
        return;
      }
      if (this.dir === '/') {
        return this.filesStore.getRoot(this.currentView.id);
      }
      const fileId = this.pathsStore.getPath(this.currentView.id, this.dir);
      return this.filesStore.getNode(fileId);
    },
    columns() {
      // Hide columns if the list is too small
      if (this.filesListWidth < 512) {
        return [];
      }
      return this.currentView?.columns || [];
    },
    totalSize() {
      // If we have the size already, let's use it
      if (this.currentFolder?.size) {
        return formatFileSize(this.currentFolder.size, true);
      }
      // Otherwise let's compute it
      return formatFileSize(this.nodes.reduce((total, node) => total + node.size || 0, 0), true);
    }
  },
  methods: {
    classForColumn(column) {
      return {
        'files-list__row-column-custom': true,
        [`files-list__row-${this.currentView.id}-${column.id}`]: true
      };
    },
    t: translate
  }
});