import { emit, subscribe } from '@nextcloud/event-bus';
import { translate } from '@nextcloud/l10n';
import Cog from 'vue-material-design-icons/Cog.vue';
import NcAppNavigation from '@nextcloud/vue/dist/Components/NcAppNavigation.js';
import NcAppNavigationItem from '@nextcloud/vue/dist/Components/NcAppNavigationItem.js';
import NcIconSvgWrapper from '@nextcloud/vue/dist/Components/NcIconSvgWrapper.js';
import { setPageHeading } from '../../../../core/src/OCP/accessibility.js';
import { useViewConfigStore } from '../store/viewConfig.ts';
import logger from '../logger.js';
import NavigationQuota from '../components/NavigationQuota.vue';
import SettingsModal from './Settings.vue';
export default {
  name: 'Navigation',
  components: {
    Cog,
    NavigationQuota,
    NcAppNavigation,
    NcAppNavigationItem,
    NcIconSvgWrapper,
    SettingsModal
  },
  props: {
    // eslint-disable-next-line vue/prop-name-casing
    Navigation: {
      type: Object,
      required: true
    }
  },
  setup() {
    const viewConfigStore = useViewConfigStore();
    return {
      viewConfigStore
    };
  },
  data() {
    return {
      settingsOpened: false
    };
  },
  computed: {
    currentViewId() {
      return this.$route?.params?.view || 'files';
    },
    currentView() {
      return this.views.find(view => view.id === this.currentViewId);
    },
    views() {
      return this.Navigation.views;
    },
    parentViews() {
      return this.views
      // filter child views
      .filter(view => !view.parent)
      // sort views by order
      .sort((a, b) => {
        return a.order - b.order;
      });
    },
    childViews() {
      return this.views
      // filter parent views
      .filter(view => !!view.parent)
      // create a map of parents and their children
      .reduce((list, view) => {
        list[view.parent] = [...(list[view.parent] || []), view];
        // Sort children by order
        list[view.parent].sort((a, b) => {
          return a.order - b.order;
        });
        return list;
      }, {});
    }
  },
  watch: {
    currentView(view, oldView) {
      if (view.id !== oldView?.id) {
        this.Navigation.setActive(view);
        logger.debug('Navigation changed', {
          id: view.id,
          view
        });
        this.showView(view);
      }
    }
  },
  beforeMount() {
    if (this.currentView) {
      logger.debug('Navigation mounted. Showing requested view', {
        view: this.currentView
      });
      this.showView(this.currentView);
    }
  },
  methods: {
    showView(view) {
      // Closing any opened sidebar
      window?.OCA?.Files?.Sidebar?.close?.();
      this.Navigation.setActive(view);
      setPageHeading(view.name);
      emit('files:navigation:changed', view);
    },
    /**
     * Expand/collapse a a view with children and permanently
     * save this setting in the server.
     */
    onToggleExpand(view) {
      // Invert state
      const isExpanded = this.isExpanded(view);
      // Update the view expanded state, might not be necessary
      view.expanded = !isExpanded;
      this.viewConfigStore.update(view.id, 'expanded', !isExpanded);
    },
    /**
     * Check if a view is expanded by user config
     * or fallback to the default value.
     */
    isExpanded(view) {
      return typeof this.viewConfigStore.getConfig(view.id)?.expanded === 'boolean' ? this.viewConfigStore.getConfig(view.id).expanded === true : view.expanded === true;
    },
    /**
     * Generate the route to a view
     */
    generateToNavigation(view) {
      if (view.params) {
        const {
          dir,
          fileid
        } = view.params;
        return {
          name: 'filelist',
          params: view.params,
          query: {
            dir,
            fileid
          }
        };
      }
      return {
        name: 'filelist',
        params: {
          view: view.id
        }
      };
    },
    /**
     * Open the settings modal
     */
    openSettings() {
      this.settingsOpened = true;
    },
    /**
     * Close the settings modal
     */
    onSettingsClose() {
      this.settingsOpened = false;
    },
    t: translate
  }
};