import { DefaultType, FileAction, Node, NodeStatus, View, getFileActions } from '@nextcloud/files';
import { showError, showSuccess } from '@nextcloud/dialogs';
import { translate as t } from '@nextcloud/l10n';
import Vue, { PropType } from 'vue';
import ArrowLeftIcon from 'vue-material-design-icons/ArrowLeft.vue';
import ChevronRightIcon from 'vue-material-design-icons/ChevronRight.vue';
import NcActionButton from '@nextcloud/vue/dist/Components/NcActionButton.js';
import NcActions from '@nextcloud/vue/dist/Components/NcActions.js';
import NcActionSeparator from '@nextcloud/vue/dist/Components/NcActionSeparator.js';
import NcIconSvgWrapper from '@nextcloud/vue/dist/Components/NcIconSvgWrapper.js';
import NcLoadingIcon from '@nextcloud/vue/dist/Components/NcLoadingIcon.js';
import CustomElementRender from '../CustomElementRender.vue';
import logger from '../../logger.js';
// The registered actions list
const actions = getFileActions();
export default Vue.extend({
  name: 'FileEntryActions',
  components: {
    ArrowLeftIcon,
    ChevronRightIcon,
    CustomElementRender,
    NcActionButton,
    NcActions,
    NcActionSeparator,
    NcIconSvgWrapper,
    NcLoadingIcon
  },
  props: {
    filesListWidth: {
      type: Number,
      required: true
    },
    loading: {
      type: String,
      required: true
    },
    opened: {
      type: Boolean,
      default: false
    },
    source: {
      type: Object,
      required: true
    },
    gridMode: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      openedSubmenu: null
    };
  },
  computed: {
    currentDir() {
      // Remove any trailing slash but leave root slash
      return (this.$route?.query?.dir?.toString() || '/').replace(/^(.+)\/$/, '$1');
    },
    currentView() {
      return this.$navigation.active;
    },
    isLoading() {
      return this.source.status === NodeStatus.LOADING;
    },
    // Sorted actions that are enabled for this node
    enabledActions() {
      if (this.source.attributes.failed) {
        return [];
      }
      return actions.filter(action => !action.enabled || action.enabled([this.source], this.currentView)).sort((a, b) => (a.order || 0) - (b.order || 0));
    },
    // Enabled action that are displayed inline
    enabledInlineActions() {
      if (this.filesListWidth < 768 || this.gridMode) {
        return [];
      }
      return this.enabledActions.filter(action => action?.inline?.(this.source, this.currentView));
    },
    // Enabled action that are displayed inline with a custom render function
    enabledRenderActions() {
      if (this.gridMode) {
        return [];
      }
      return this.enabledActions.filter(action => typeof action.renderInline === 'function');
    },
    // Default actions
    enabledDefaultActions() {
      return this.enabledActions.filter(action => !!action?.default);
    },
    // Actions shown in the menu
    enabledMenuActions() {
      // If we're in a submenu, only render the inline
      // actions before the filtered submenu
      if (this.openedSubmenu) {
        return this.enabledInlineActions;
      }
      const actions = [
      // Showing inline first for the NcActions inline prop
      ...this.enabledInlineActions,
      // Then the rest
      ...this.enabledActions.filter(action => action.default !== DefaultType.HIDDEN && typeof action.renderInline !== 'function')].filter((value, index, self) => {
        // Then we filter duplicates to prevent inline actions to be shown twice
        return index === self.findIndex(action => action.id === value.id);
      });
      // Generate list of all top-level actions ids
      const topActionsIds = actions.filter(action => !action.parent).map(action => action.id);
      // Filter actions that are not top-level AND have a valid parent
      return actions.filter(action => !(action.parent && topActionsIds.includes(action.parent)));
    },
    enabledSubmenuActions() {
      return this.enabledActions.filter(action => action.parent).reduce((arr, action) => {
        if (!arr[action.parent]) {
          arr[action.parent] = [];
        }
        arr[action.parent].push(action);
        return arr;
      }, {});
    },
    openedMenu: {
      get() {
        return this.opened;
      },
      set(value) {
        this.$emit('update:opened', value);
      }
    },
    /**
     * Making this a function in case the files-list
     * reference changes in the future. That way we're
     * sure there is one at the time we call it.
     */
    getBoundariesElement() {
      return document.querySelector('.app-content > .files-list');
    },
    mountType() {
      return this.source._attributes['mount-type'];
    }
  },
  methods: {
    actionDisplayName(action) {
      if ((this.gridMode || this.filesListWidth < 768 && action.inline) && typeof action.title === 'function') {
        // if an inline action is rendered in the menu for
        // lack of space we use the title first if defined
        const title = action.title([this.source], this.currentView);
        if (title) return title;
      }
      return action.displayName([this.source], this.currentView);
    },
    async onActionClick(action) {
      let isSubmenu = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : false;
      // If the action is a submenu, we open it
      if (this.enabledSubmenuActions[action.id]) {
        this.openedSubmenu = action;
        return;
      }
      const displayName = action.displayName([this.source], this.currentView);
      try {
        // Set the loading marker
        this.$emit('update:loading', action.id);
        Vue.set(this.source, 'status', NodeStatus.LOADING);
        const success = await action.exec(this.source, this.currentView, this.currentDir);
        // If the action returns null, we stay silent
        if (success === null || success === undefined) {
          return;
        }
        if (success) {
          showSuccess(t('files', '"{displayName}" action executed successfully', {
            displayName
          }));
          return;
        }
        showError(t('files', '"{displayName}" action failed', {
          displayName
        }));
      } catch (e) {
        logger.error('Error while executing action', {
          action,
          e
        });
        showError(t('files', '"{displayName}" action failed', {
          displayName
        }));
      } finally {
        // Reset the loading marker
        this.$emit('update:loading', '');
        Vue.set(this.source, 'status', undefined);
        // If that was a submenu, we just go back after the action
        if (isSubmenu) {
          this.openedSubmenu = null;
        }
      }
    },
    execDefaultAction(event) {
      if (this.enabledDefaultActions.length > 0) {
        event.preventDefault();
        event.stopPropagation();
        // Execute the first default action if any
        this.enabledDefaultActions[0].exec(this.source, this.currentView, this.currentDir);
      }
    },
    isMenu(id) {
      return this.enabledSubmenuActions[id]?.length > 0;
    },
    t
  }
});