import { translate } from '@nextcloud/l10n';
import NcCheckboxRadioSwitch from '@nextcloud/vue/dist/Components/NcCheckboxRadioSwitch.js';
import Vue from 'vue';
import { useFilesStore } from '../store/files.ts';
import { useSelectionStore } from '../store/selection.ts';
import FilesListTableHeaderActions from './FilesListTableHeaderActions.vue';
import FilesListTableHeaderButton from './FilesListTableHeaderButton.vue';
import filesSortingMixin from '../mixins/filesSorting.ts';
import logger from '../logger.js';
export default Vue.extend({
  name: 'FilesListTableHeader',
  components: {
    FilesListTableHeaderButton,
    NcCheckboxRadioSwitch,
    FilesListTableHeaderActions
  },
  mixins: [filesSortingMixin],
  props: {
    isMtimeAvailable: {
      type: Boolean,
      default: false
    },
    isSizeAvailable: {
      type: Boolean,
      default: false
    },
    nodes: {
      type: Array,
      required: true
    },
    filesListWidth: {
      type: Number,
      default: 0
    }
  },
  setup() {
    const filesStore = useFilesStore();
    const selectionStore = useSelectionStore();
    return {
      filesStore,
      selectionStore
    };
  },
  computed: {
    currentView() {
      return this.$navigation.active;
    },
    columns() {
      // Hide columns if the list is too small
      if (this.filesListWidth < 512) {
        return [];
      }
      return this.currentView?.columns || [];
    },
    dir() {
      // Remove any trailing slash but leave root slash
      return (this.$route?.query?.dir || '/').replace(/^(.+)\/$/, '$1');
    },
    selectAllBind() {
      const label = this.isNoneSelected || this.isSomeSelected ? this.t('files', 'Select all') : this.t('files', 'Unselect all');
      return {
        'aria-label': label,
        checked: this.isAllSelected,
        indeterminate: this.isSomeSelected,
        title: label
      };
    },
    selectedNodes() {
      return this.selectionStore.selected;
    },
    isAllSelected() {
      return this.selectedNodes.length === this.nodes.length;
    },
    isNoneSelected() {
      return this.selectedNodes.length === 0;
    },
    isSomeSelected() {
      return !this.isAllSelected && !this.isNoneSelected;
    }
  },
  methods: {
    ariaSortForMode(mode) {
      if (this.sortingMode === mode) {
        return this.isAscSorting ? 'ascending' : 'descending';
      }
      return null;
    },
    classForColumn(column) {
      return {
        'files-list__column': true,
        'files-list__column--sortable': !!column.sort,
        'files-list__row-column-custom': true,
        [`files-list__row-${this.currentView.id}-${column.id}`]: true
      };
    },
    onToggleAll(selected) {
      if (selected) {
        const selection = this.nodes.map(node => node.fileid.toString());
        logger.debug('Added all nodes to selection', {
          selection
        });
        this.selectionStore.setLastIndex(null);
        this.selectionStore.set(selection);
      } else {
        logger.debug('Cleared selection');
        this.selectionStore.reset();
      }
    },
    t: translate
  }
});